import React, { Component } from 'react';
import {
  Badge,
  Button,
  Card,
  CardBody,
  CardFooter,
  CardHeader,
  Col,
  Collapse,
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
  Fade,
  Form,
  FormGroup,
  FormText,
  FormFeedback,
  Container,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupButtonDropdown,
  InputGroupText,
  Label,
  Row,
  Pagination,
  PaginationItem,
  PaginationLink,
  Table,
} from 'reactstrap';
import axios from 'axios';
import {BootstrapTable, TableHeaderColumn,TableDataColumn} from 'react-bootstrap-table';

class Forms extends Component {
  constructor() {
    super()
    

    this.state = {
      companyRegNo : '',
      companyName : '',
      companyAddress : '',
      companyEmail : '',
      companyContact : '',
      created:'',
      errors: {},
      companies:[],
      btnText:'Save',
      btnColor:'dark',
      lastCompany:[],
      
      myElement : ( 
        this.MuiTable
       )
    
    };
    this.onChange = this.onChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
  }

  componentDidMount(){
    this.getCompanies();
  }

  onChange(e) {
    this.setState({ [e.target.name]: e.target.value })
  }
  async onSubmit(e){
    e.preventDefault();
    if(!this.state.btnText.localeCompare('Save')){
      this.addCompany();
    }
    else{
      this.updateCompany();
    }
    
  }
  getCompanies(){
    fetch('/getcompanies')
    .then(res => res.json())
    .then(json =>{
      this.setState({ 
        companies : json
      });
    });
  }
  handleUpdate = () => {
    this.forceUpdate(); 
   };

//Add a new company
addCompany= async()=>{
  await axios.post('/addcompany',{
    companyRegNo : this.state.companyRegNo,
    companyName: this.state.companyName,
    companyAddress: this.state.companyAddress,
    companyEmail:this.state.companyEmail,
    companyContact: this.state.companyContact
   }).then(res =>{
    var status=res.data.status;
    if(!status.localeCompare('SuccessfullyAdded')){
      alert('Successfully Added');
      
      this.addCompanyPayRow();
    }
    else{
      alert('Bad Credentials')
    }
  })
   
   this.getCompanies()
}
//get last delivery note

// add a payment row for company
addCompanyPayRow= async()=>{
  await axios.get('/getlastcompany').then(res=>{
    this.setState({
      lastCompany : res.data
    })
  })
  await axios.post('/addpay',{
    company : this.state.lastCompany[0].companyId,
   }).then(res =>{
    var status=res.data.status;
    if(!status.localeCompare('UnSuccessfull')){
      alert('Error occured please contact developer');
    }
  })
}
//Update company details
updateCompany= async () =>{
  await axios.post('/updatecompany',{
    companyRegNo : this.state.companyRegNo,
    companyName: this.state.companyName,
    companyAddress: this.state.companyAddress,
    companyEmail:this.state.companyEmail,
    companyContact: this.state.companyContact
   }).then(res =>{
    var status=res.data.status;
    if(!status.localeCompare('SuccessfullyAdded')){
      alert('Successfully Updated');
    }
    else{
      alert('Bad Credentials')
    }
  })
   
   this.getCompanies() 
}

//Event handlers for table
onRowSelect=(rowKeys)=>{
  this.setState({
    companyRegNo : rowKeys.companyRegNo,
    companyName: rowKeys.companyName,
    companyAddress: rowKeys.companyAddress,
    companyEmail:rowKeys.companyEmail,
    companyContact: rowKeys.companyContact,
    btnText:'Update',
    btnColor:'info'
  })
}

onAfterDeleteRow= async (rowKeys)=> {
  await axios.post('/deletecompany',{
    companyRegNo : rowKeys
  }).then(res =>{
    var status=res.data.status;
    if(!status.localeCompare('SuccessfullyAdded')){
      alert('Successfully Deleted');
      this.getCompanies();
    }
    else{
      alert('Failed To Delete')
    }
  })
}

selectRowProp={
  mode:'radio',
  onSelect: this.onRowSelect,
  bgColor:'yellow'
}

options = {
  afterDeleteRow: this.onAfterDeleteRow  // A hook for after droping rows.

};

clearButton=()=>{
  this.setState({
    companyRegNo : '',
    companyName : '',
    companyAddress : '',
    companyEmail : '',
    companyContact : '',
    btnColor:'dark',
    btnText:'Save'
  })
}
  
  render() {
    var companies=this.state.companies;
    return (
      <div className="animated fadeIn">
        <Container>
        <Row>
          <Col xs="12" sm="6">
            <Card>
              <CardHeader>
                <strong>Company</strong>
              </CardHeader>
              <CardBody style={{backgroundColor:"#d9d9d9"}}>
                <Form onSubmit={this.onSubmit}>
                <FormGroup>
                  <Label htmlFor="company">Company Name</Label>
                  <Input type="text" name="companyName"
                  value={this.state.companyName} onChange={this.onChange}/>
                </FormGroup>
                <FormGroup>
                  <Label htmlFor="regNo">Registration Number</Label>
                  <Input type="text" name="companyRegNo" 
                  value={this.state.companyRegNo} onChange={this.onChange}/>
                </FormGroup>
                <FormGroup>
                  <Label htmlFor="address">Address</Label>
                  <Input type="text" name="companyAddress" 
                  value={this.state.companyAddress} onChange={this.onChange} />
                </FormGroup>
                <FormGroup row>
                    <Col md="3">
                      <Label htmlFor="hf-email">Email</Label>
                    </Col>
                    <Col xs="12" md="9">
                      <Input type="email" id="hf-email" name="companyEmail" autoComplete="email" 
                      value={this.state.companyEmail} onChange={this.onChange}/>
                    </Col>
                  </FormGroup>
                <FormGroup xs="5">
                  <Label htmlFor="contact">Contact No</Label>
                  <Input type="text" name="companyContact" 
                  value={this.state.companyContact} onChange={this.onChange} />
                </FormGroup>
                  <FormGroup row className="my-0">
                  <Col xs="6">
                    <FormGroup>
                        <Button block color={this.state.btnColor} onClick={this.handleUpdates}>{this.state.btnText}</Button>
                    </FormGroup>
                  </Col>
                  <Col xs="6">
                    <FormGroup>
                      <Button block color="dark" onClick={this.clearButton}>clear</Button>
                    </FormGroup>
                  </Col>
                  </FormGroup>
                  </Form>
              </CardBody>
            </Card>
          </Col>
  
          <Col  xs="6" ys="6" lg="9" xl="6">
          <Card responsive bordered>
              <CardHeader>
                <i className="fa fa-align-justify"></i> Companies
              </CardHeader>
              <CardBody style={{backgroundColor:"#d9d9d9"}}>
              <div>
                <BootstrapTable data={this.state.companies } height='375' scrollTop={'Bottom'} search={true}
                selectRow={this.selectRowProp} deleteRow={true} options={this.options}
                tableStyle={ { border: '#0000FF 2.5px solid' } }
                containerStyle={ { border: '#FFBB73 2.5px solid' } }
                headerStyle={ { border: 'red 1px solid' } }
                bodyStyle={ { border: 'green 1px solid' }}>
     
                <TableHeaderColumn width="200" tdStyle={ { whiteSpace: 'normal' } } dataField='companyName' 
                      >Company Name</TableHeaderColumn>
                <TableHeaderColumn width="200" tdStyle={ { whiteSpace: 'normal' } } dataField='companyRegNo'
                      isKey>Register No</TableHeaderColumn>
               <TableHeaderColumn width="150" tdStyle={ { whiteSpace: 'normal' } } dataField='companyContact'
                      >Contact Number</TableHeaderColumn>
                <TableHeaderColumn width="200" tdStyle={ { whiteSpace: 'normal' } } dataField='companyEmail'
                      >Email</TableHeaderColumn>
                <TableHeaderColumn width="250" tdStyle={ { whiteSpace: 'normal' } } dataField='companyAddress'
                      >Address</TableHeaderColumn>
                <TableHeaderColumn width="150" tdStyle={ { whiteSpace: 'normal' } } dataField='companyRegDate'
                      >Registered Date</TableHeaderColumn>

                </BootstrapTable>
 
            </div>
              </CardBody>
            </Card>
    
          </Col>
        
        </Row>  
        </Container>
       
        
      </div>
    );
  }
  
}

export default Forms;
